import React from 'react';
import './Services.css'
import Card from '../Card/Card';
import HeartEmoji from '../../img/heartemoji.png';
import Glasses from '../../img/glasses.png';
import {motion} from 'framer-motion'
import Resume from './Resume.pdf';


const Services = () => {
  const transition = {duration : 1, type: 'spring'}
  return (
   <div className="services" id='Services'>
    {/* left side */}
    <div className="awesome">
    <span>My Awesome</span>
    <span>Services</span>
    <span>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Nulla, ipsum!
        <br />
        Lorem ipsum dolor sit amet consectetur.
    </span>
    <a href={Resume} download>
    <button className='button s-button'>Download CV</button>
    </a>
    <div className="blur s-blur1" style={{background:"#ABF!FF94"}}></div>
    </div>

    {/* right side  */}
    
    <div className="cards">
      <motion.div 
      whileInView={{left: '14rem'}}
      initial={{left: '25rem'}}
      transition={transition}
      
      style={{left:'28rem'}}>
      <Card
      emoji= {HeartEmoji}
      heading={'Design'}
      detail={'HTML, CSS, JAVASCRIPT, REACT JS'}
      />
      </motion.div>

     {/* second card  */}

     <motion.div 
      
      whileInView={{left: "-4rem"}}
      initial={{left: '-11rem', top: "12rem"}}
      transition={transition}

     style={{top:"12rem", left: "8rem"}}>
      <Card
      emoji={Glasses}
      heading={'Developer'}
      detail={" i'm Developer for Html, Css, JavaScript, React "}
      />
     </motion.div>
      
   {/* 3td card  */}

   <motion.div 
     whileInView={{left: "12rem"}}
     initial={{top: '19rem', left: "25rem"}}
     transition={transition}

   style={{top:"19rem", left: '25rem'}}>
    <Card
    emoji={Glasses }
    heading={"UI/UX"}
    detail={"lorem ispum dummy text usually use in Lorem ipsum dolor"}
    />

   </motion.div>
    
    <div className="blur s-blur2" style={{background: "var(--purple"}}></div>

    </div>
   </div>
  );
}

export default Services;
