import React from 'react'
import './Intro.css'
import Github from '../../img/github.png';
import LinkedIn from '../../img/linkedin.png';
import Instagram from '../../img/instagram.png';
import Vector1 from '../../img/Vector1.png';
import Vector2 from '../../img/Vector2.png';
import boys2 from '../../img/boys2.png';
import FlotingDiv from '../FlotingDiv/FlotingDiv';
import thumbup from '../../img/thumbup.png';
import Crown from '../../img/crown.png'
import Glassesimoji from '../../img/glassesimoji.png'

import {themeContext} from '../../Context'
import { useContext } from 'react';
import {motion} from 'framer-motion'
import {Link} from 'react-scroll'
function Intro() {

  const transition = {duration : 2, type: 'spring'}

  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;

  return (
  <div className="intro" id='Home'>
    <div className="i-left"> 
    <div className="i-name">
        <span style={{color:darkMode? 'white': ''}}>Hy! I Am</span>    
        <span>MD. Saif Ali Khan</span>
        <span>Frontend Developer with React js and high level of experience in web designing 
            and development
        </span>
    </div>
     
     
    

    <Link spy={true} to="Contact" smooth={true} >
    <button className=" button i-button">Hire me</button>
               </Link>           


       <div className="i-icons">
        <img src={Github} alt="" />
        <img src={LinkedIn} alt="" />
        <a href=" https://instagram.com/mr.saif_ali_6?igshid=ZDdkNTZiNTM=">
        <img src={Instagram} alt="" />
        </a>
       </div>

     </div>
    <div className="i-right"> 
    <img src={Vector1} alt="" />
    <img src={Vector2} alt="" />
    <img src={boys2} alt=""  />
    < motion.img 
    initial={{left: '-36%'}}
    whileInView={{left: '-24%'}}
    transition={{transition}}
    src={Glassesimoji} alt="" />
     
    <motion.div 
     initial={{top: '-4%', left: '74%'}}
     whileInView={{left:'68%'}}
     transition={{transition}}

    style={{top:'-4%', left:'68%' }} 
    className='floating-div'
    >
    <FlotingDiv  image={Crown} txt1='Web' txt2='Developer' />
    </motion.div>
    <motion.div 
    initial= {{ left:'9rem', top:'18rem'}}
    whileInView={{left:'0rem'}}
    transition={transition}

    style={{top:'18rem', left:'0rem'}}
    className='floating-div'
    >
     <FlotingDiv image={thumbup} txt1='Best design'/>
    </motion.div>
    {/* blur divs */}
   <div
   className="blur" style={{backgroundColor:'rgb(238 210 255',  background: '#e2bbfa'}}>
   </div>
   <div className="blur" style={{
    background: '#c1f5ff', top:'17rem',
    width: '21rem',
    height: '11rem',
    left:'-9rem'
   }}></div>
    </div>    
  </div>
  )
}

export default Intro
