import React from 'react';
import './Testimonial.css';
import { Swiper, SwiperSlide } from 'swiper/react';
import "swiper/css";

import { Pagination } from 'swiper';
import "swiper/css/pagination";
import Amjad from '../../img/Amjad.jpg';
import Sahil from '../../img/sahil.jpg';
import Irshad from '../../img/irshad.jpg'
import Saif from '../../img/saif.jpg'


const Testimonial = () => {

    const clients = [
        {
            img: Amjad,
             review: "This is Amjad Ali, and he is a Flutter developer and he has created sum projects and wants to do something new and is doing something"
        },
        {
            img: Sahil,
             review: "This is Sahil Rahman , and he is a master of Data science and he has created sum projects and wants to do something new "
           
        },
        {
            img:Irshad,
             review: "This is MD Irshad , and he is a Flutter developer and he has created sum projects and wants to do something new and is doing something"
           
        },
        {
            img: Saif,
             review: "This is Amjad Ali, and he is a React developer and he has created sum projects and wants to do something new and is doing something "
        },
    ];

  return (
   <div className="t-wrapper" id='Testimonials'>
   <div className="t-heading">
   <span>My Developer</span>
      <span>Team</span>
    <div className='blur t-blurl' style={{background:"skyblue"}}></div>
    <div className='blur t-blurl' style={{background:"var(--purple)"}}></div>
  
   </div>
  {/* slider */}
   
   <Swiper 
//     install swiper
 modules={[Pagination]}
 slidesPerView={1}
 pagination= {{clickable:true}}
   >

    {clients.map((client, index) => {
     return(
        <SwiperSlide key={index}>
            <div className='testimonial'>
                <img src={client.img} alt="" />
                <span>{client.review}</span> 
            </div>
            </SwiperSlide>
     )
    })}


   </Swiper>

   </div>
  );
}



export default Testimonial;
